import * as React from 'react';

import {AppContext, AppProviderState, initialAppState, styles} from './AppContext';
import {CustomDriver, CustomRace} from '../../models/F1Models';
import {PointsYear, Scenario} from '../Scenario/ScenarioModels';

import DriverUtils from '../../utils/DriverUtils';
import {yearPointsMappings} from '../../models/F1Constants';

import cloneDeep = require('lodash/cloneDeep');

interface AppProviderProps {}

// Create provider
export class AppContextProvider extends React.Component<AppProviderProps, AppProviderState> {
    constructor(props: AppProviderProps) {
        super(props);
        this.state = initialAppState;
        this.onChangePointsYear = this.onChangePointsYear.bind(this);
        this.onChangeYear = this.onChangeYear.bind(this);
        this.onSelectScenario = this.onSelectScenario.bind(this);
        this.onToggleDoublePoints = this.onToggleDoublePoints.bind(this);
        this.mOnToggleDriverActive = this.mOnToggleDriverActive.bind(this);
        this.onTogglePersistSetting = this.onTogglePersistSetting.bind(this);
        this.onToggleMenu = this.onToggleMenu.bind(this);
    }

    public render() {
        return (
            <AppContext.Provider
                value={{
                    state: this.state,
                    styles,
                    onChangePointsYear: this.onChangePointsYear,
                    onChangeYear: this.onChangeYear,
                    onSelectScenario: this.onSelectScenario,
                    onToggleDoublePoints: this.onToggleDoublePoints,
                    mOnToggleDriverActive: this.mOnToggleDriverActive,
                    onTogglePersistSetting: this.onTogglePersistSetting,
                    onToggleMenu: this.onToggleMenu,
                }}
            >
                {this.props.children}
            </AppContext.Provider>
        );
    }

    private mOnToggleDriverActive(driver: CustomDriver): void {
        const {
            allDrivers,
            allRaces,
            selectedYear,
            selectedPointsYear,
            doublePointsFinale,
            driverSeasonMapping,
        } = this.state;

        const clonedDrivers = cloneDeep(allDrivers);
        const updatingDriver: CustomDriver = clonedDrivers.find((aDriver: CustomDriver) => {
            return aDriver.driverId === driver.driverId;
        }) as CustomDriver;
        updatingDriver.active = driver.active;

        DriverUtils.allocateDriverPointsForYear(
            allRaces[selectedYear],
            clonedDrivers,
            driverSeasonMapping[selectedYear],
            selectedYear,
            selectedPointsYear,
            doublePointsFinale,
        );

        this.setState({allDrivers: clonedDrivers});
    }

    private onChangeYear(event: React.ChangeEvent<HTMLSelectElement>) {
        const selectedYear = Number(event.target.value);
        // console.log(event);
        const {
            allDrivers,
            allRaces,
            doublePointsFinale,
            driverSeasonMapping,
            persistPointsSettings,
            selectedPointsYear,
        } = this.state;

        // Clone values
        const clonedDrivers: CustomDriver[] = cloneDeep(allDrivers);
        const yearRaces = allRaces[selectedYear];
        const clonedRaces = cloneDeep(yearRaces);
        const updatedSelectedPointsYear = persistPointsSettings
            ? selectedPointsYear
            : yearPointsMappings[selectedYear].year;
        const updateDoublePointsFinale = persistPointsSettings
            ? doublePointsFinale
            : yearPointsMappings[updatedSelectedPointsYear].doublePointsFinale;
        DriverUtils.allocateDriverPointsForYear(
            clonedRaces,
            clonedDrivers,
            driverSeasonMapping[selectedYear],
            selectedYear,
            updatedSelectedPointsYear,
            updateDoublePointsFinale,
        );

        this.setState({
            allDrivers: clonedDrivers,
            doublePointsFinale: updateDoublePointsFinale,
            selectedPointsYear: updatedSelectedPointsYear,
            selectedYear,
        });
    }

    private onChangePointsYear(event: React.ChangeEvent<HTMLSelectElement>) {
        const selectedPointsYear = Number(event.target.value) as PointsYear;
        const {
            allDrivers,
            allRaces,
            doublePointsFinale,
            driverSeasonMapping,
            persistPointsSettings,
            selectedYear,
        } = this.state;

        // Clone values
        const clonedDrivers: CustomDriver[] = cloneDeep(allDrivers);
        const yearRaces = allRaces[selectedYear];
        const clonedRaces = cloneDeep(yearRaces);
        const updateDoublePointsFinale = persistPointsSettings
            ? doublePointsFinale
            : yearPointsMappings[selectedPointsYear].doublePointsFinale;
        DriverUtils.allocateDriverPointsForYear(
            clonedRaces,
            clonedDrivers,
            driverSeasonMapping[selectedYear],
            selectedYear,
            selectedPointsYear,
            updateDoublePointsFinale,
        );

        this.setState({
            allDrivers: clonedDrivers,
            doublePointsFinale: updateDoublePointsFinale,
            selectedPointsYear,
        });
    }

    private onToggleDoublePoints(event: React.SyntheticEvent<HTMLInputElement>) {
        const doublePointsFinale = event.currentTarget.checked;

        const {
            allDrivers,
            allRaces,
            selectedPointsYear,
            selectedYear,
            driverSeasonMapping,
        } = this.state;

        // Clone values
        const clonedDrivers: CustomDriver[] = cloneDeep(allDrivers);
        const yearRaces = allRaces[selectedYear];
        const clonedRaces = cloneDeep(yearRaces);
        DriverUtils.allocateDriverPointsForYear(
            clonedRaces,
            clonedDrivers,
            driverSeasonMapping[selectedYear],
            selectedYear,
            selectedPointsYear,
            doublePointsFinale,
        );

        this.setState({allDrivers: clonedDrivers, doublePointsFinale});
    }

    private onTogglePersistSetting() {
        const {persistPointsSettings} = this.state;
        this.setState({persistPointsSettings: !persistPointsSettings});
    }

    private onSelectScenario(scenario: Scenario) {
        const {inactiveDriverIds, doublePointsFinale, selectedPointsYear, selectedYear} = scenario;

        const {allDrivers, allRaces, driverSeasonMapping} = this.state;
        const clonedDrivers: CustomDriver[] = cloneDeep(allDrivers);
        const clonedRaces: CustomRace[] = cloneDeep(allRaces[selectedYear]);

        DriverUtils.toggleActiveDrivers(clonedDrivers, inactiveDriverIds || []);

        DriverUtils.allocateDriverPointsForYear(
            clonedRaces,
            clonedDrivers,
            driverSeasonMapping[selectedYear],
            selectedYear,
            selectedPointsYear,
            doublePointsFinale,
        );

        this.setState({
            allDrivers: clonedDrivers,
            doublePointsFinale,
            selectedPointsYear,
            selectedYear,
        });
    }

    private onToggleMenu() {
        // console.log('onToggleMenu');
        this.setState({menuOpen: !this.state.menuOpen});
    }
}



