import * as React from 'react';

import {WithStylesProps, styles} from '../Context/AppContext';

import {withStyles} from '@material-ui/core/styles';

const StyledLayoutMain: React.CSSProperties = {
    display: 'flex',
    flexDirection: 'column',
    flex: 1,
};

class LayoutMain extends React.PureComponent<WithStylesProps> {
    public render() {
        const {classes, children} = this.props;
        return (
            <div style={StyledLayoutMain} className={classes}>
                {children}
            </div>
        );
    }
}

export default withStyles(styles)(LayoutMain);



