import * as React from 'react';

import {AppContext, ContextProps} from '../components/Context/AppContext';

import Helmet from 'react-helmet';
import LayoutContent from '../components/LayoutContent/LayoutContent';
import LayoutMain from '../components/gatsby/LayoutMain';

interface WrapperProps {
    children: () => any;
    data: {
        site: {
            siteMetadata: {
                title: string;
                description: string;
            };
        };
    };
}

export default class IndexLayout extends React.PureComponent<WrapperProps> {
    public render() {
        const {data, children} = this.props;
        return (
            <LayoutContent>
                <Helmet
                    title={data.site.siteMetadata.title}
                    meta={[
                        {name: 'description', content: data.site.siteMetadata.description},
                        {
                            name: 'keywords',
                            content: 'gatsbyjs, gatsby, javascript, sample, something',
                        },
                    ]}
                />
                <AppContext.Consumer>
                    {(context: ContextProps) => {
                        const {styles} = context;
                        return (
                            <div style={styles.root}>
                                <LayoutMain>{children()}</LayoutMain>
                            </div>
                        );
                    }}
                </AppContext.Consumer>
            </LayoutContent>
        );
    }
}

export const query = graphql`
    query IndexLayoutQuery {
        site {
            siteMetadata {
                title
                description
            }
        }
    }
`;



