import CssBaseline from '@material-ui/core/CssBaseline';
import {MuiThemeProvider} from '@material-ui/core/styles';
import React from 'react';
import getPageContext from './getPageContext';

function withRoot(Component: any) {
    class WithRoot extends React.Component {
        private muiPageContext = null;

        constructor(props: any) {
            super(props);
            this.muiPageContext = props.muiPageContext || getPageContext();
        }

        public componentDidMount() {
            // Remove the server-side injected CSS.
            const jssStyles = document.querySelector('#server-side-jss');
            if (jssStyles && jssStyles.parentNode) {
                jssStyles.parentNode.removeChild(jssStyles);
            }
        }

        public render() {
            // MuiThemeProvider makes the theme available down the React tree thanks to React context.
            return (
                <MuiThemeProvider
                    theme={(this.muiPageContext as any).theme}
                    sheetsManager={(this.muiPageContext as any).sheetsManager}
                >
                    {/* CssBaseline kickstart an elegant, consistent, and simple baseline to build upon. */}
                    <CssBaseline />
                    <Component {...this.props} />
                </MuiThemeProvider>
            );
        }
    }

    // (WithRoot as any).propTypes = {
    //     muiPageContext: (PropTypes).object,
    // };

    return WithRoot;
}

export default withRoot;



