import * as React from 'react';

import {WithStylesProps, styles} from '../Context/AppContext';

import {AppContextProvider} from '../Context/AppContextProvider';
import {withStyles} from '@material-ui/core/styles';

const StyledLayoutRoot: React.CSSProperties = {
    display: 'flex',
    flexDirection: 'column',
    minHeight: '100vh',
};

class LayoutRoot extends React.PureComponent<WithStylesProps> {
    public render() {
        const {classes, children} = this.props;
        return (
            <AppContextProvider>
                <div style={StyledLayoutRoot} className={classes}>
                    {children}
                </div>
            </AppContextProvider>
        );
    }
}

export default withStyles(styles)(LayoutRoot);



