import * as React from 'react';

import {AppContext, ContextProps, WithStylesProps, styles} from '../Context/AppContext';
import {Theme, createMuiTheme} from '@material-ui/core/styles';

import AppBar from '@material-ui/core/AppBar';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import CssBaseline from '@material-ui/core/CssBaseline';
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import IconButton from '@material-ui/core/IconButton';
import LayoutRoot from '../gatsby/LayoutRoot';
import List from '@material-ui/core/List';
import MenuIcon from '@material-ui/icons/Menu';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import classNames from 'classnames';
import withRoot from '../../withRoot';
import {withStyles} from '@material-ui/core/styles';

export const theme: Theme = createMuiTheme();

class LayoutContent extends React.Component<WithStylesProps> {
    constructor(props: WithStylesProps) {
        super(props);
    }
    public render() {
        const {classes, children} = this.props;

        return (
            <LayoutRoot classes={classes}>
                <CssBaseline />
                <AppContext.Consumer>
                    {(context: ContextProps) => {
                        const {state, onToggleMenu} = context;
                        const {menuOpen} = state;
                        return (
                            <div className={classes.root}>
                                <AppBar
                                    position="absolute"
                                    className={classNames(
                                        classes.appBar,
                                        menuOpen && classes.appBarShift,
                                    )}
                                >
                                    <Toolbar disableGutters={!menuOpen} className={classes.toolbar}>
                                        <IconButton
                                            color="inherit"
                                            aria-label="Open drawer"
                                            onClick={onToggleMenu}
                                            className={classNames(
                                                classes.menuButton,
                                                menuOpen && classes.menuButtonHidden,
                                            )}
                                        >
                                            <MenuIcon />
                                        </IconButton>
                                        <Typography
                                            variant="title"
                                            color="inherit"
                                            noWrap
                                            className={classes.title}
                                        >
                                            Eff Wun Point 5
                                        </Typography>
                                        {/* <IconButton color="inherit">
                                            <Badge badgeContent={0} color="secondary">
                                                <NotificationsIcon />
                                            </Badge>
                                        </IconButton> */}
                                    </Toolbar>
                                </AppBar>
                                <Drawer
                                    variant="permanent"
                                    classes={{
                                        paper: classNames(
                                            classes.drawerPaper,
                                            !menuOpen && classes.drawerPaperClose,
                                        ),
                                    }}
                                    open={menuOpen}
                                >
                                    <div className={classes.toolbarIcon}>
                                        <IconButton onClick={onToggleMenu}>
                                            <ChevronLeftIcon />
                                        </IconButton>
                                    </div>
                                    <Divider />
                                    <List>{this.renderMainListItems()}</List>
                                    <Divider />
                                    <List>{this.renderSecondaryListItems()}</List>
                                </Drawer>
                                <main className={classes.content}>
                                    <div className={classes.appBarSpacer} />
                                    {children}
                                </main>
                            </div>
                        );
                    }}
                </AppContext.Consumer>
            </LayoutRoot>
        );
    }

    private renderMainListItems() {
        return <div>---</div>;
    }

    private renderSecondaryListItems() {
        return <div>---</div>;
    }
}

export default withRoot(withStyles(styles)(LayoutContent));



